@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    font-size: 62.5%;
}

body{
     font-family: 'Kumbh Sans', sans-serif;
     min-height: 100vh;
     background-color:#34ebd8; 
     display: flex;
     align-items: center;
     justify-content: center;
    
}

.text-component{
    width: 60vw;
    height: 80vh;
    background-color:#fff;
    border-radius: 2rem;
    min-width: 50vw;
    box-shadow: 10px 5px 3px 5px rgba(0,0,0,.5);
}


.title{
    font-size: xx-large;
    font-weight: 700;
    font-family:'Kumbh Sans', sans-serif ;
    padding-top: 2rem;
    margin-left: 4rem;
    color: black;
    text-align: center;
    text-transform: capitalize;
}

.title-2{
    font-size: xx-large;
    font-weight: 700;
    font-family:'Kumbh Sans', sans-serif ;
    padding-top: 2rem;
    margin-left: 4rem;
    color: teal;
    text-transform: capitalize;
}

.subtitle{
    font-size:large;
    font-weight: 400;
    font-family: 'Kumbh Sans', sans-serif;
    color: teal;
    text-align: justify;
    padding-top: 2rem;
    margin-left: 4rem;
    margin-right: 4rem;
    text-align: justify;
}
.More-content{
    margin-left: 4rem;
    font-size: 1.3rem;
    margin-top: 5rem;
}
.but{
    margin-top: 3rem;
    text-align: center;
}
.but #but1{
    text-align: center;
    font-size: 2rem;
    padding: 1rem;
    background-color: #34ebd7;
    border-radius: 1.3rem;
    font-weight: 700;

}
.but #but1:hover{
    background-color: rgb(243, 123, 31);
    color: white;
    cursor: pointer;
}
.but #but1 >a{
    text-decoration: none;
}
